}.blue .n3-btn-primary {
  background-color: #54b4eb !important;
  border-color: #54b4eb !important;
}
.blue .n3-btn-primary.n3-btn-active {
  background-color: #1684c2 !important;
  border-color: #1684c2 !important;
}
.blue .n3-nav-tabs-pills > li.n3-nav-tabs-active > a {
  background-color: #a7d2f5 !important;
}
.blue .n3-breadcrumb {
  padding: 0;
}
.blue .n3-breadcrumb > .n3-breadcrumb-active {
  color: #54b4eb;
}
.blue .n3-form-control:focus {
  border: 1px solid #54b4eb;
  outline: 0;
}
.blue .n3-datepicker-dateRange-item-active {
  background-color: #54b4eb !important;
}
.blue .n3-datepicker-dateRange span:hover {
  background-color: #a7d2f5 !important;
}
.blue .n3-switch-on {
  background-color: #54b4eb !important;
  transition: background 0.55s ease-in-out, box-shadow 0.55s ease-in-out, color 0.55s ease-in-out, -webkit-box-shadow 0.55s ease-in-out;
}
.blue .n3-page-item-active {
  background-color: #1684c2 !important;
  border-color: #1684c2 !important;
}
.blue .n3-checkbox-checked .n3-checkbox-inner {
  border-color: transparent;
  background-color: #1684c2;
}
.blue .n3-tree-active > .n3-tree-node__content {
  color: #1684c2;
}
.blue .page-content .n3-btn-group > .n3-btn,
.blue .n3-dropdown-menu > li > a {
  min-width: 220px;
}
.blue .page-content .n3-btn {
  width: 150px;
}
.blue .header {
  background: #54b4eb;
}
.blue .sidebar .n3-subnav {
  border: 1px solid #7da8f5;
  background-color: #207ecc;
}
